import React from 'react';
import empty_img from '../../images/no item.webp'
import { NavLink } from 'react-router-dom';
import './Empty.css'

const Empty = () => {


    return (
        <div className='empty-cart' >
            <div className="empty-img">
                <img src={empty_img} alt="" />
            </div>
            <div className="empty-inf">
                <span >Looks like you have no items in your shopping cart. <br /> <NavLink to='/products'>Click here</NavLink> to continue shopping</span>
            </div>
        </div>
    )
}

export default Empty
