import React, { useState } from "react";
import "./Filter.css";
import { useDispatch } from "react-redux";
import { setProducts } from "../../store/ProductSlice";
import data from "../../data.json";

const Filter = () => {
  const [filter, setFilter] = useState(false);
  const dispatch = useDispatch();
  const running = data.filter((item) => item.category === "RUNNING");
  const formal = data.filter((item) => item.category === "FORMAL");
  const men = data.filter((item) => item.gender === "MEN");
  const women = data.filter((item) => item.gender === "WOMEN");
  const kid = data.filter((item) => item.gender === "KIDS");

  return (
    <>
      <button onClick={() => setFilter(true)} className="open-filter">
        filter
      </button>
      <div className={filter ? "filter expand-filter" : "filter"}>
        <button className="close-filter" onClick={() => setFilter(false)}>
          X
        </button>
        <span onClick={() => dispatch(setProducts(data))}>All</span>
        <span onClick={() => dispatch(setProducts(running))}>Running</span>
        <span onClick={() => dispatch(setProducts(formal))}>Formal</span>
        <span onClick={() => dispatch(setProducts(men))}>Men</span>
        <span onClick={() => dispatch(setProducts(women))}>Women</span>
        <span onClick={() => dispatch(setProducts(kid))}>Kid's</span>
      </div>
    </>
  );
};

export default Filter;
