import './Navbar.css'
import { useState } from 'react';
import CloseIcon from '@mui/icons-material/Close';
import MenuIcon from '@mui/icons-material/Menu';
import { NavLink } from 'react-router-dom'
import ShoppingBagOutlinedIcon from '@mui/icons-material/ShoppingBagOutlined';
import { useSelector } from 'react-redux';

const Navbar = () => {
    const [menu, setMenu] = useState(false);
    const cart = useSelector(state => state.cart);
    return (
        <nav className={menu ? 'header expand-header' : 'header'} >
            <div className="logo">
                <NavLink to='/' className='nav-link'>Apna Shop</NavLink>
            </div>
            <div className={menu ? 'nav-1 nav-1-v' : 'nav-1'}>
                <div>
                    <NavLink to='/products' className='nav-link'>Shop</NavLink>
                </div>
                <div>
                    <NavLink to='/about' className='nav-link'>About</NavLink>
                </div>
                <div>
                    <NavLink to='/contact' className='nav-link'>Contact</NavLink>
                </div>
            </div>
            <div className="nav-2">
                <div className='cart-bag'>
                    <NavLink to='/cart' className='nav-link'> <ShoppingBagOutlinedIcon className='nav-link' /> </NavLink>
                    <span>{cart.length}</span>
                </div>

                <div className="menu">
                    <button onClick={() => setMenu(!menu)}>{menu ? <CloseIcon /> : <MenuIcon />}</button>
                </div>
            </div>
        </nav>
    )
}

export default Navbar
