import './Contact.css'
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
import YouTubeIcon from '@mui/icons-material/YouTube';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import FacebookIcon from '@mui/icons-material/Facebook';
import EmailIcon from '@mui/icons-material/Email';


const Contact = () => {
    return (
        <form className='contact-page' onSubmit={(event) => event.preventDefault()}>
            <div className="contact-inf">
                <h1 className='fw-bolder'>Apna <br /> Shop</h1>
                <h3>
                    YOU CAN CONTACT US DIRECTLY AT
                </h3>
                <span><EmailIcon className='email-icon' /> gauravchavhan05@gmail.com</span>
                <div>
                    <span>Or You Can Write Us Via Contact Form </span> <br />
                    <span>We Answers As Quick As Possible</span>
                </div>
                <div className="contact-social">
                    <span>
                        <FacebookIcon />
                    </span>
                    <span>
                        <LinkedInIcon />
                    </span>
                    <span>
                        <YouTubeIcon />
                    </span>
                    <span>
                        <TwitterIcon />
                    </span>
                    <span>
                        <InstagramIcon />
                    </span>

                </div>
            </div>
            <div className="contact-form">
                <h2>Contact us</h2>
                <div className="contact-form-content">
                    <div className="name">
                        <input type="text" name="" placeholder='Your Full Name' id="" required />
                    </div>
                    <div className="email">
                        <input type="email" placeholder='Email' required />
                    </div>
                    <div className="subject">
                        <input type="text" placeholder='Subject' required />
                    </div>
                    <div className="contact-msg">
                        <input type="text" placeholder='Write Something....' required />
                    </div>
                    <div className="submit">
                        <button type='submit'>  Send Message</button>
                    </div>
                </div>
            </div>
        </form>
    )
}

export default Contact
