import './Footer.css'
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
import YouTubeIcon from '@mui/icons-material/YouTube';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import FacebookIcon from '@mui/icons-material/Facebook';
import CopyrightIcon from '@mui/icons-material/Copyright';
import { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';

const Footer = () => {
  const [currentDate, setCurrentDate] = useState([]);

  useEffect(() => {
    setCurrentDate(new Date().getFullYear());
  }, []);

  return (
    <footer>
      <div className="footer-nav">
        <div className="footer-nav-lines">
          <span>get connected with us on social networks</span>
        </div>
        <div className="footer-nav-social">

          <span>
            <InstagramIcon className='footer-social-icon' />
          </span>
          <span>
            <TwitterIcon className='footer-social-icon' />
          </span>
          <span>
            <LinkedInIcon className='footer-social-icon' />
          </span>
          <span>
            <YouTubeIcon className='footer-social-icon' />
          </span>
          <span>
            <FacebookIcon className='footer-social-icon' />
          </span>
        </div>

      </div>
      <div className="footer-content">

        <div className="footer-about">
          <h4>About Company</h4>
          <p>
            Step into Style and Comfort with Our Premium Shoe Collection. Discover the Perfect Blend of Fashion and Functionality – Elevate Your Every Step. Explore a World of Quality Craftsmanship and Trendsetting Designs. Experience Footwear Excellence with Us Today!
          </p>
        </div>
        <div className="footer-links">
          <h4>Userful Links</h4>
          <NavLink to='/' className='nav-link'>Home</NavLink>
          <NavLink to='/about' className='nav-link'>About</NavLink>
          <NavLink to='/products' className='nav-link'>Shop</NavLink>
          <NavLink to='/contact' className='nav-link'>Contact</NavLink>
          <NavLink to='/cart' className='nav-link'>Cart</NavLink>

        </div>
        <div className="footer-category">
          <h4>Category</h4>
          <span>Runnigs</span>
          <span>Formal</span>
          <span>Kid's</span>
          <span>Men</span>
          <span>Women</span>
        </div>
        <div className="footer-subscribe">
          <h4>Subscribe</h4>
          <form onSubmit={(e) => e.preventDefault()}>
            <input type="email" placeholder='example0@gmail.com' required />
            <button type='submit'>Subscribe</button>
          </form>
        </div>
      </div>
      <div className="footer-copyright">
        <span>{<CopyrightIcon />} {currentDate} Copyright apnashop.com</span>
      </div>
    </footer>
  )
}

export default Footer
