import React from 'react'
import './About.css'
import Brand from '../../components/brand/Brand'
import Special from '../../components/special/Special'
import man_1 from '../../images/man_3.jpg'
import man_2 from '../../images/men_2.jpg'
import man_3 from '../../images/man_1.jpg'


const About = () => {
  return (
    <div style={{ display: 'flex', flexDirection: 'column', gap: '100px' }}>
      <div>
        <Brand />
      </div>
      <div>
        <Special />
      </div>
      <div className='team'>
        <h2>Meet Our Team</h2>
        <div>
          <div className="man_1">
            <img src={man_1} alt="" />
          </div>
          <div className="man_2">
            <img src={man_2} alt="" />
          </div>
          <div className="man_3">
            <img src={man_3} alt="" />
          </div>
        </div>
      </div>
    </div>
  )
}

export default About
