import React from 'react'
import brand from '../../images/two.png'
import './Brand.css'
import { useNavigate } from 'react-router-dom'

const Brand = () => {
  const navigate = useNavigate();
  return (
    <div className='brand'>
      <div className="brand-img">
        <img src={brand} alt="" />
      </div>
      <div className="brand-inf">
        <h1>New Arrival Running shoes for the men and women</h1>
        <p>The quality of shoes is a paramount factor that significantly impacts both comfort and durability. A well-crafted pair of shoes exhibits a meticulous attention to detail, featuring precise stitching, high-quality materials, and expert construction. Premium materials such as genuine leather, breathable textiles, and sturdy rubber soles not only enhance the aesthetic appeal but also contribute to the overall longevity of the shoes. Superior cushioning and arch support provide an unparalleled level of comfort, making each step a delightful experience. When the elements of design and functionality come together harmoniously, the result is a pair of shoes that not only elevates personal style but also stands the test of time, becoming a reliable companion for various activities and occasions.</p>
        <button className='brand-btn' onClick={() => navigate('/products')}>Show Now</button>
      </div>

    </div>
  )
}

export default Brand
