import './Cart.css'
import { useDispatch, useSelector } from 'react-redux'
import { checkout, decrement, increment, removeCartItem } from '../../store/CartSlice';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle';
import HighlightOffIcon from '@mui/icons-material/HighlightOff';
import Empty from '../../components/empty-cart/Empty';
import { useNavigate } from 'react-router-dom';

const Cart = () => {
    const cart = useSelector(state => state.cart);
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const handleRemove = (id) => {
        dispatch(removeCartItem(id))
    }
    const handleIncrement = (id) => {
        dispatch(increment(id))
    }
    const handleDecrement = (id) => {
        dispatch(decrement(id))
    }
    const handleCheakOut = () => {

        alert('your order has been placed...!')
        dispatch(checkout())
    }
    const subtotal = cart.reduce((acc, curr) => {
        return acc + curr.price * curr.quantity;
    }, 0)

    const totalAmount = subtotal - 10;


    if (cart.length <= 0) {
        return <Empty />
    }
    return (
        <div className='cart'>
            <div className="cart-products">
                {
                    cart.map((item) => {
                        return (
                            <div className="cart-item" key={item.id}>
                                <img src={item.imageURL} alt="" />
                                <div className="cart-item-name">
                                    <span className='cart-item-long-name'>{item.name.length > 10 ? item.name.slice(0, 15).concat('...') : item.name}</span>
                                    <span className='cart-item-short-name'>{item.name.slice(0, 5)}</span>
                                    <span>Size : {item.size}</span>
                                </div>
                                <div className="quantity">
                                    <span className='increment-btn' onClick={() => handleIncrement(item.id)}><AddCircleIcon /></span>
                                    <span >{item.quantity}</span>
                                    <span className='decrement-btn' onClick={() => handleDecrement(item.id)}><RemoveCircleIcon /></span>
                                </div>
                                <span className='remove-item' onClick={() => handleRemove(item.id)}><HighlightOffIcon /></span>
                            </div>
                        )
                    })
                }
            </div>
            <div className="sub-total">
                <h3>Subtotal</h3>
                <div className="sub-total-details">
                    <div className="total-items">
                        <span>Total Items : </span>
                        <span>{cart.length}</span>
                    </div>
                    <div className="total-amount">
                        <span>Total Price : </span>
                        <span>${subtotal.toFixed(2)}</span>
                    </div>
                    <div className="discount">
                        <span>Discount : </span>
                        <span>$10</span>
                    </div>
                    <div className="final-price">
                        <span>Total : </span>
                        <span>${totalAmount}.00</span>
                    </div>
                </div>
                <div className="checkout">
                    <button onClick={handleCheakOut}>Cheakout</button>
                    <button onClick={() => navigate('/products')}>Return to shop</button>
                </div>
            </div>
        </div>
    )
}

export default Cart
