import { createSlice } from "@reduxjs/toolkit";


const Cartslice = createSlice({
    name: 'cart',
    initialState: [],
    reducers: {
        addToCart: (state, action) => {
            state.push(action.payload)
        },
        removeCartItem: (state, action) => {
            return state.filter(item => item.id !== action.payload)
        },
        increment: (state, action) => {
            let existItem = state.find(item => item.id === action.payload);
            if (existItem && existItem.quantity < existItem.items_left) {
                existItem.quantity++;

            }
            else {
                window.alert(`only ${existItem.items_left} itmes availabe`)
                existItem.quantity = existItem.items_left;
            }
        },
        decrement: (state, action) => {
            let existItem = state.find(item => item.id === action.payload);
            if (existItem && existItem.quantity > 1) {
                existItem.quantity--;
            }
            else {
                existItem.quantity = 1;
            }
        },
        checkout: (state, action) => {
            return state = [];
        }

    }
});

export default Cartslice.reducer;
export const {
    addToCart,
    removeCartItem,
    increment,
    decrement,
    checkout
} = Cartslice.actions;