import React from 'react'
import StarOutlineIcon from "@mui/icons-material/StarOutline";
import StarIcon from "@mui/icons-material/Star";
import StarHalfIcon from "@mui/icons-material/StarHalf";
import './Star.css'

const Star = ({ rate }) => {
  const rateStar = Array.from({ length: 5 }, (item, index) => {
    let number = index + 0.5;
    return (
      <span key={index}>
        {
          rate >= index + 1 ? <StarIcon className='start-icon' /> : rate >= number ? <StarHalfIcon className='start-icon' /> : <StarOutlineIcon className='start-icon' />
        }
      </span>
    )
  })
  return (
    <div className="rating-icons">
      <span>{rateStar}</span>
    </div>
  )
}

export default Star;
