import { useState } from 'react';
import './View.css'
import Button from 'react-bootstrap/Button';
import ArrowRightAltRoundedIcon from '@mui/icons-material/ArrowRightAltRounded';
import Modal from 'react-bootstrap/Modal';
import { useNavigate } from 'react-router-dom';

const Example = ({ product }) => {

    const [show, setShow] = useState(false);
    const navigate = useNavigate();
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);


    return (
        <>
            <Button variant="primary" className='w-100' onClick={handleShow} style={{ backgroundColor: '#29CF7E', border: 'none', borderRadius: '0px' }}>
                Quick View
            </Button>

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title className='fs-6'>{product.name} - {product.gender} - {product.category}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="product-body-quick">
                        <div className='quick-view-image'>
                            <img src={product.imageURL} width='50%' alt="" />
                        </div>
                        <div className="quick-view-details">
                            <p> $ {product.price}</p>
                            <p>{product.brand} </p>
                            <p className='text-danger'>Only {product.items_left <= 9 ? "0" + product.items_left : product.items_left} itmes' left</p>
                        </div>
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <Button className='btn' style={{ color: '#fff', backgroundColor: '#29CF7E' }} variant='light' onClick={() => navigate(`/single-product/${product.id}`)}>
                        View this product's full details <ArrowRightAltRoundedIcon />
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    );
}

export default Example;