import './Hero.css'
import img1 from '../../images/one.png'
import img2 from '../../images/two.png'
import img5 from '../../images/three.png'
import { useState } from 'react'
import { useNavigate } from 'react-router-dom'

const Hero = () => {

    const images = [img1, img2, img5]
    const [heroImg, setHeroImg] = useState(img1);
    const navigate = useNavigate()

    return (
        <>
            <div className='hero'>
                <div className="hero-heading">
                    <h1>“Shoes are Boring. <br /> <span className='fw-bold'> Wear Sneakers.</span>”</h1>
                    <p>"Explore our curated selection of stylish and comfortable shoes, where every step is a statement of elegance."</p>
                    <button className='shop-btn' onClick={() => navigate('/products')}>Shop Now</button>
                </div>
                <div className="hero-img">
                    <img src={heroImg} alt="" className='hero-section-img' />
                    <div className="hero-img-option" >
                        {
                            images.map((item, index) => {
                                return (

                                    <div key={index} className='hero-img-item'>
                                        <img src={item} width='5%' alt='' onMouseOver={() => setHeroImg(item)} />
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>

            </div>

        </>
    )
}

export default Hero
