import { useNavigate } from 'react-router-dom';
import './Special.css'
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { setProducts } from '../../store/ProductSlice';
import data from '../../data.json'

const Special = () => {
  const { products } = useSelector(state => state.products)
  const specialProducts = products.filter(item => item.special);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(setProducts(data));
  }, [dispatch])

  return (
    <div className="specials">
      <h1 className='text-center mb-4 mt-2'>Special Products</h1>
      <div className='special-products'>
        {
          specialProducts?.map((item) => {
            return (
              <div className="special-item" key={item.id} onClick={() => navigate(`/single-product/${item.id}`)}>
                <img src={item.imageURL} width="20%" alt={item.name} className='special-item-img' />
                <span className="special-item-name">
                  {item.name.slice(0, 23).concat("...")}
                </span>
                <span>{item.brand} {item.gender}</span>
                <b>$ {item.price}</b>

              </div>
            )
          })
        }
      </div>
    </div>
  )
}

export default Special
